import React, { useState, useEffect } from "react"
import Layout from "../components/Layout"
import SEO from "../components/SEO"
import ResumenNacional from "../components/ResumenNacional"
import DatosPartido from "../components/DatosPartido"
import PreguntasFrecuentes from "../components/PreguntasFrecuentes"

export default function Index() {
  const [datos, setDatos] = useState({
    fecha: "",
    nacional: {
      confirmados: "",
      defunciones: "",
    },
    morena: {
      confirmados: "",
      defunciones: "",
    },
    pri: {
      confirmados: "",
      defunciones: "",
    },
    pan: {
      confirmados: "",
      defunciones: "",
    },
    independiente: {
      confirmados: "",
      defunciones: "",
    },
    mc: {
      confirmados: "",
      defunciones: "",
    },
    prd: {
      confirmados: "",
      defunciones: "",
    },
    pes: {
      confirmados: "",
      defunciones: "",
    },
  })
  useEffect(() => {
    //Lectura de la api
    fetch("https://covidporpartido.firebaseio.com/.json")
      .then(response => response.json())
      .then(json => {
        const fechaAux = json.ultimaAct
        var fechaDefuncionesAux = fechaAux
        const confirmadosAux = json.Nacional.confirmados[fechaAux].acumulados
        const confirmadosMorenaAux =
          json.Morena.confirmados[fechaAux].acumulados
        const confirmadosPriAux = json.PRI.confirmados[fechaAux].acumulados
        const confirmadosPanAux = json.PAN.confirmados[fechaAux].acumulados
        const confirmadosIndAux =
          json.Independiente.confirmados[fechaAux].acumulados
        const confirmadosMcAux = json.MC.confirmados[fechaAux].acumulados
        const confirmadosPrdAux = json.PRD.confirmados[fechaAux].acumulados
        const confirmadosPesAux = json.PES.confirmados[fechaAux].acumulados
        var defuncionesAux
        try {
          /*Intenta obtener las defunciones actualizadas al ultimo dia,
        pero normalmente en fin de semana no las actualizan*/
          defuncionesAux = json.Nacional.defunciones[fechaAux].acumuladas
        } catch (error) {
          /*En caso de que no esten actualizadas al ultimo dia, obtiene 
        las defunciones actualizadas al dia anterior*/

          /*Convierte la ultima fecha de string a date, para poder restarle un dia,
        despues la regresa a string para poder ser usada en el JSON*/
          var mes = fechaAux.slice(3, 5)
          var dia = fechaAux.slice(0, 2)
          if (dia === 1) {
            //Si es el primero del mes
            mes--
            mes = "0" + mes
            mes = mes.slice(-2)
          } else {
            //Si no es el primer dia del mes
            dia--
            dia = "0" + dia
            dia = dia.slice(-2)
          }
          const fechaDefuncionesAux =
            dia + "-" + mes + "-" + fechaAux.slice(6, 10)
          defuncionesAux =
            json.Nacional.defunciones[fechaDefuncionesAux].acumuladas
        }
        const defuncionesMorenaAux =
          json.Morena.defunciones[fechaDefuncionesAux].acumuladas
        const defuncionesPriAux =
          json.PRI.defunciones[fechaDefuncionesAux].acumuladas
        const defuncionesPanAux =
          json.PAN.defunciones[fechaDefuncionesAux].acumuladas
        const defuncionesIndAux =
          json.Independiente.defunciones[fechaDefuncionesAux].acumuladas
        const defuncionesMcAux =
          json.MC.defunciones[fechaDefuncionesAux].acumuladas
        const defuncionesPrdAux =
          json.PRD.defunciones[fechaDefuncionesAux].acumuladas
        const defuncionesPesAux =
          json.PES.defunciones[fechaDefuncionesAux].acumuladas
        setDatos({
          fecha: fechaAux,
          nacional: {
            confirmados: confirmadosAux,
            defunciones: defuncionesAux,
          },
          morena: {
            confirmados: confirmadosMorenaAux,
            defunciones: defuncionesMorenaAux,
          },
          pri: {
            confirmados: confirmadosPriAux,
            defunciones: defuncionesPriAux,
          },
          pan: {
            confirmados: confirmadosPanAux,
            defunciones: defuncionesPanAux,
          },
          independiente: {
            confirmados: confirmadosIndAux,
            defunciones: defuncionesIndAux,
          },
          mc: {
            confirmados: confirmadosMcAux,
            defunciones: defuncionesMcAux,
          },
          prd: {
            confirmados: confirmadosPrdAux,
            defunciones: defuncionesPrdAux,
          },
          pes: {
            confirmados: confirmadosPesAux,
            defunciones: defuncionesPesAux,
          },
        })
      })
  }, [])

  //Constantes
  const datosNacionales = [
    {
      id: "confirmados",
      titulo: `${datos.nacional.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",")} casos confirmados`,
      subtitulo: null,
      borde: " border-red-500",
    },
    {
      id: "defunciones",
      titulo: `${datos.nacional.defunciones
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ",")} defunciones`,
      subtitulo: `${(
        (datos.nacional.defunciones * 100) /
        datos.nacional.confirmados
      ).toFixed(1)}% de letalidad`,
      borde: " border-black",
    },
  ]

  const datosResumenPartido = [
    {
      id: 1,
      partido: "Morena",
      confirmados: datos.morena.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
      defunciones: datos.morena.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    },
    {
      id: 2,
      partido: "PRI",
      confirmados: datos.pri.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
      defunciones: datos.pri.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    },
    {
      id: 3,
      partido: "PAN",
      confirmados: datos.pan.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
      defunciones: datos.pan.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    },
    {
      id: 4,
      partido: "Independiente",
      confirmados: datos.independiente.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
      defunciones: datos.independiente.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    },
    {
      id: 5,
      partido: "Movimiento Ciudadano",
      confirmados: datos.mc.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
      defunciones: datos.mc.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    },
    {
      id: 6,
      partido: "PRD",
      confirmados: datos.prd.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
      defunciones: datos.prd.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    },
    {
      id: 7,
      partido: "Encuentro Social",
      confirmados: datos.pes.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
      defunciones: datos.pes.confirmados
        .toString()
        .replace(/\B(?=(\d{3})+(?!\d))/g, ","),
    },
  ]

  //Funciones
  function generarResumenNacional(item) {
    const { id, titulo, subtitulo, borde } = item
    return (
      <ResumenNacional
        key={id}
        titulo={titulo}
        subtitulo={subtitulo}
        borde={borde}
      />
    )
  }

  function generarDatosPartido(item) {
    const { id, partido, confirmados, defunciones } = item
    return (
      <DatosPartido
        key={id}
        partido={partido}
        confirmados={confirmados}
        defunciones={defunciones}
      />
    )
  }

  return (
    <Layout>
      <SEO title="Inicio" />
      <section className="mb-12">
        <h2 className="text-4xl font-semibold">Datos nacionales</h2>
        <p className="mb-3">{`Datos actualizados al da ${datos.fecha}`}</p>
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
          {datosNacionales.map(generarResumenNacional)}
        </div>
      </section>
      {/* <section className="mb-12">
        {datosGraficasNacionales.map(generarGraficasNacionalesl)}
      </section> */}

      <section className="mb-12">
        <h2 className="text-4xl font-semibold my-4">Datos por partido</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {datosResumenPartido.map(generarDatosPartido)}
        </div>
      </section>
      {/* <section className="mb-12">
        <h2 className="text-4xl font-semibold my-4">Tabla general</h2>
      </section> */}
      <PreguntasFrecuentes />
    </Layout>
  )
}
