import React from "react"

export default function ResumenNacional(props) {
  const { titulo, subtitulo, borde } = props

  return (
    <div className={`my-4 bg-white rounded-sm border-l-4 p-4 ${borde}`}>
      <h3 className="text-3xl">{titulo}</h3>
      {subtitulo && <p>{subtitulo}</p>}
    </div>
  )
}
